@import url(https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,400;1,900&display=swap);
body {
  font-family: 'Poppins', sans-serif;
  margin: 0;
  padding: 0;
  background-color: rgb(228, 222, 222) !important;
  width: 100%;
  min-height: 100vh;
}
:root{
  --bg-color: rgba(10, 9, 9, 0.2);
}

.container {
 width: 1200px;
 margin:auto;
  display: flex;
  flex-direction: row;
  box-sizing: border-box;
  flex-wrap: wrap;
}
.hr{
  background-color: rgb(202, 201, 199) !important;
  height: 1px;
  width: 100%;
}

@media(max-width:576px){
  .container{
    flex-direction: column !important;
    width: 100%;
  }
  .card{
    width: 92% !important;
  }
  .modal-content{
    width: 90vw !important;
  }
 

}


.header{
background-color:white;
height: 4em;
width: 100%;
display: flex;
padding: 0 2em;
box-sizing: border-box;
justify-content: space-between;

}

.logo h4{

 font-weight: bold;
 margin: 0;
 padding: 0;
 font-size: 2em;
 position: relative;
 letter-spacing: 0.1em;
}
.logo h4::after{
    content: "Write your dream";
    position: absolute;
    top: 90%;
    right: 1%;
  font-size: 35%;
 white-space: nowrap;
 letter-spacing: 0.001em;
  
  
}
footer{
    position: absolute;
    bottom: 0;
    width: 100%;
    height: 4em;
    
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 1em;
    box-sizing: border-box;
    position: fixed;
}
.btn{
    height: 3.5em;
    width: 3.5em;
    display: grid;
    place-items: center;
    border-radius: 50%;
    background-color: teal;
    color: white;
    z-index: 100000000;
    box-shadow: 3px 3px 10px rgba(0,0,0,0.3);

}


.card{
 width: 22%;
 height: 400px;
 background-color: white !important;
 margin: 1em;
 border-radius: 4px;
 box-shadow: 0px 0px 1px #3b2828d8,0px 0px 1px #b4b1b1d8;
 position: relative;
}
.card-header{
  padding: .7em 1em;
  
}
.card-timing{
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-size: 0.7em;
    
}
.card-header h4{
    font-size: 1.2em;
}
.title{
    margin: 0;
    padding: 0;
    font-size: 1.2em;
    font-weight: bold;
    letter-spacing: 0.1em;
}
.card-body{
    padding: 0.5em 1em;
}
.hr{
    background-color: rgb(202, 201, 199) !important;
    height: 1px;
    width: 100%;
}
.card-body p{
    font-size: 0.94em;
    letter-spacing: 0.08em;
}
.card-body p::first-letter{
    background-color: rgb(30, 28, 44); 
    color:white;
    padding: 1px 10px;
    margin-right:2px ;
    font-size: 1.4em;
    border-radius: 4px;
    text-transform: uppercase;
    
}
.card-footer{
position: absolute;
bottom: 0;
width: 100%;
box-sizing: border-box;
height: 2.3em;

}
.footer-btn{
    display: flex;
    justify-content: space-between;
    grid-column-gap: 1em;
    -webkit-column-gap: 1em;
            column-gap: 1em;
    align-items: center;
    padding: 0 1em;
    margin-top: 0.4em;
}

@media(max-width:576px){
    .card-body p::first-letter {
      font-size: 15px;
    }
}

.modal{
    position: fixed;
    height: 100vh;
    width: 100%;
    top:0;
    left: 0;
    background-color: rgba(0, 0, 0, 0.7);
    z-index: 100;
    overflow: hidden;
    display: flex;
    justify-content: center;
    align-items: center;
    transition: 0.3s all ease-in-out;
}
.dismiss{
    cursor:pointer;
}
.modal-content{
    width: 25%;
    height: 80vh;
    background-color: white;
    border-radius: 10px;
    position: relative;
}
.modal-header{
    font-size: 1.1em;
    padding: 0.4em 0.8em;
    display: flex;
    justify-content: space-between;
    
}
.modal-body{
    padding-top: 1.8em ;
    justify-content: center;
}
.form-group{
    display: flex;
    justify-content: center;
    flex-direction: column;
    padding: 0 2em;
    box-sizing: border-box;
    position: relative;
    margin-bottom: 2em;
}
.form-input{
    width: 97%;
    outline: none;
    border: none;
    padding: 0.8em 0.5em;
    border-bottom: 2px solid rgba(0, 0, 0, 0.555);
    background-color: transparent;
   
}
.form-area{
    width: 97%;
    font-size: 110%;
    outline: none;
    border: none;
    padding: 0.8em 0.5em;
    padding-left: 0.3em;
    /* border: 2px solid rgba(0, 0, 0, 0.555); */
    background-color: transparent;
    /* overflow: scroll; */
   
}

label{
  
    letter-spacing: 0.09em;
    font-size: 0.8em;
    font-weight: bold;
    transition: 0.2s all ease-in-out;
    position: absolute;
    z-index: 2;
    top:-14px;
     left: 10%;
    /* transform: translateY(-50%); */
    
}
.err{
    font-size: 0.8em;
     color: rgba(255, 0, 128, 0.979);
     display: block;
     margin-top: 3px;
}
.modal-footer{
    
    position: absolute;
    bottom: 0;
    width: 100%;
    height: 3em;
    box-sizing: border-box;
}
.footer-content{
    display: flex;
    justify-content:space-between;
    align-items: center;
    position: absolute;
    bottom: 0;
    padding: 0.5em 1em;
    box-sizing: border-box;
    width: 100%;
    
}
.btn-submit{
    background-color: transparent;
outline: none;
cursor: pointer;
border: none;
padding: 0.5em 1em;
background-color: rgba(0, 0, 0, 1);
color: white;
border-radius: 4px;

}
.btn-submit:hover{
    opacity: 0.9;
}
.btn-close{
    background-color: rgb(248, 10, 129) !important;
outline: none;
cursor: pointer;
border: none;
padding: 0.5em 0.8em;
border-radius: 4px;
color: white;

}
@media(max-width:576px){
    .modal-content{
        height: 70vh;
    }
}
